$midnight: #2c3e50;
$clouds: #ecf0f1;

/*-----------fonts---------*/
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Regular.eot');
    src: local('Avenir Next Cyr Regular'), local('AvenirNextCyr-Regular'),
    url('../fonts/AvenirNextCyr-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/AvenirNextCyr-Regular.woff2') format('woff2'),
        url('../fonts/AvenirNextCyr-Regular.woff') format('woff'),
        url('../fonts/AvenirNextCyr-Regular.ttf') format('truetype');
    font-weight: normal;
}
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Demi.eot');
    src: local('Avenir Next Cyr Demi'), local('AvenirNextCyr-Demi'),
    url('../fonts/AvenirNextCyr-Demi.eot?#iefix') format('embedded-opentype'),
    url('../fonts/AvenirNextCyr-Demi.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Demi.woff') format('woff'),
    url('../fonts/AvenirNextCyr-Demi.ttf') format('truetype');
    font-weight: 500;
}
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Bold.eot');
    src: local('Avenir Next Cyr Bold'), local('AvenirNextCyr-Bold'),
    url('../fonts/AvenirNextCyr-Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/AvenirNextCyr-Bold.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Bold.woff') format('woff'),
    url('../fonts/AvenirNextCyr-Bold.ttf') format('truetype');
    font-weight: 700;
}
/*--------//fonts--------*/
* {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
html {
  font-size:62.5%;
}
header, main, footer {
  width: 100%;
  height: auto;
}
.gridContainer {
  min-width: 320px;
  max-width: 1400px;
  margin: 0 auto;
  }
h1 {
  font:normal 3.8rem 'AvenirNextCyr';
  color: #F4A876;
  line-height: 57px;
  text-transform: uppercase;
}
h2 {
  font:normal 3.2rem 'AvenirNextCyr';
  color: #F4A876;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.05em;
}
h3 {
  font:normal 1.8rem 'AvenirNextCyr';
  line-height: 25px;
  color: #5C5C5C;
}
h4 {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #787878;
}
p {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 24px;
  text-align: center;
  color: #000000;
}
a {
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
}
button {
  cursor: pointer;
}
.wrapper img {
  width: 100%;
}
.colorOrange {
  color: #F4A876;
}
/*--------------------------- header ---------------------------*/
.flex .navMob {
  display: flex;
  justify-content: space-between;
}
.navMob .blockLogo {
  margin:20px 10px;
}
.navMob .blockBasket {
  margin:20px 10px;
}
nav a {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #FFFFFF;
}
.menu-phone .one {
  display: flex;
  height:70px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
}
.menu-phone .navMenuUp {
  background: #ffffff;
}
.one .blockSearch {
  margin:15px 0;
  position:absolute;
  top:0;
  left:45px;
  z-index:1;
}
.one .blockMonLang {
  width:50%;
  margin:20px 0;
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  top:5px;
  right:0;
}
.navMenuUp a {
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 19px;
  color: #F4A876;
}
.callback .number {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #5C5C5C;
}
.blockCallback .linkWhite {
  padding: 12px 13px;
  border: 1px solid #F4A876;
  text-align: center;
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #F4A876;
}
.linkWhite, .callback {
  margin: 15px;
}
.menu-phone .blockCallback {
  display:flex;
  background-color: #ffffff;
}
.blockMonLang form, .loginLink {
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 19px;
  color: #F4A876;
}
.blockMonLang .loginLink {
  padding: 0;
}
.blockSearch .formSearch {
  display:flex;
  padding: 5px;
  background: #ffffff;
}
.formSearch input[type="search"]:hover {
  width: 225px;
}
.blockSearch .formSearch:hover {
  border:.1rem solid #F4A876;
}
.formSearch input[type="search"] {
  width:0px;
  border-right:.2rem solid #E5E5E5;
  transition-property: width, border;
  transition-duration: 2s, 2s;
  transition-timing-function: ease, linear;
}
.formSearch input[type="submit"] {
  text-indent: -9999px;
  width: 35px;
  height:30px;
  background: url('../img/magnifying_glass 1.svg') right no-repeat;
  background-size: 70%;
}
/*--------------- screenSlider ----------------*/
.wrapper .screenSlider {
  position: relative;
  width:100vw;
  margin-bottom: 40px;
}
.screenSlider .blockText {
  position:absolute;
  padding:40px 10px;
  z-index: 1;
}
.blockText p {
  color:#FFFFFF;
  text-align: left;
}
.blockText .text1 {
  font:normal 3.6rem 'AvenirNextCyr';
  margin-bottom: 60px;
  position: relative;
}
.blockText .text1::after {
  position: absolute;
  content:'';
  width:135px;
  height:5px;
  z-index: 1;
  background-color:#F4A876;
  bottom:-30px;
  left:0;
}
.blockText .textBold {
  font:800 3.6rem 'AvenirNextCyr';
}
.blockText .text2 {
  margin-bottom: 30px;
}
.blockText .saleLink {
  padding:15px;
  font:normal 1.6rem 'AvenirNextCyr';
  text-align: center;
  color:#ffffff;
  background: #F4A876;
  position: absolute;
  bottom: 15px;
}
.screenSlider .slider {
  height:100%;
}
/*--------------- //screenSlider ----------------*/
/*--------------- newProduct -------------*/
.wrapper .newProduct {
  border-top:.1rem solid #F4A876;
  padding-bottom:70px;
  box-shadow: 0px 24px 75px rgba(244, 168, 118, 0.2);
}
.flex1 h2 {
  position: relative;
  width: 220px;
  top: -22px;
  margin: 0 auto;
  background: #fff;
}
.textProd h3 {
  padding: 40px 10px;
  margin-right: 10px;
  text-align: left;
  border-bottom: .1rem solid #F4A876;
}
.textProd .yesProd,
.textProd .maybeProd,
.textProd .notProd {
  position:relative;
  padding:5px 30px;
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 15px;
  bottom:14.5px;
  background: #ffffff;
  color: #52BD10
}
.textProd .maybeProd {
  color: #E7A806;
}
.textProd .notProd {
  color: #ED5959;
}
.yesProd i,
.maybeProd i,
.notProd i {
  background: url('../img/Component_1.svg') center no-repeat;
  position:absolute;
  left:10px;
  width: 15px;
  height:15px;
}
.yesProd .yes {
  background: url('../img/Component_1.svg') center no-repeat;
}
.maybeProd .maybe {
  background: url('../img/Component_2.svg') center no-repeat;
}
.notProd .not {
  background: url('../img/Component_3.svg') center no-repeat;
}
.textProd table {
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 15px;
  color:#5C5C5C;
  width: 100%;
  height:140px;
  padding:10px;
}
table tr {
  width:100%;
  margin:20px;
}
table .producer {
  color:#F4A876;
}
table tr td {
  width:50%;
  margin:20px 10px;
}
table tr td:last-child {
  text-align: right;
}
.textProd .blockPay {
  display:flex;
  align-items: center;
  justify-content: space-between;
}
.blockPay .size,
.blockPay .payLink {
  margin:10px;
}
.blockPay .size {
  width:130px;
}
.blockPay .payLink {
  width:160px;
}
.blockPay .size {
  font:normal 600 2.8rem 'AvenirNextCyr';
  line-height:31px;
  color: #5C5C5C;
}
.blockPay .payLink,
.blockPay1 .payLink1 {
  font:normal 1.6rem 'AvenirNextCyr';
  letter-spacing: 0.15em;
  color: #F4A876;
  position:relative;
  padding:10px 35px 10px 63px;
  border: .1rem solid #F4A876;
}
.payLink .payIcon,
.payLink1 .payIcon1 {
  position:absolute;
  width:25px;
  height:25px;
  left:18px;
  bottom:7px;
  background: url('../img/shopping-cart_orange.svg') center no-repeat;
}
.flex1 .meter {
  position:relative;
  width:40px;
  height:30px;
  font:normal 1.6rem 'AvenirNextCyr';
  color: #F4A876;
  left: calc(50vw - 20px);
  top:20px;
}
.meter span {
  font:normal 2.4rem 'AvenirNextCyr';
  line-height: 33px;
  letter-spacing: 0.055em;
  color: #F4A876;
}
/*----------------------- //newProduct -----------------------*/
/*--------------- popularProduct -------------*/
.wrapper .popularProduct {
  margin-top:105px;
  margin-bottom:108px;
  border-top:.1rem solid #F4A876;
}
.flex2 h2 {
  position:relative;
  width:270px;
  top: -44px;
  margin: 0 auto;
  background: #fff;
}
.slider2 .article1 {
  margin:0 5px;
  padding:10px;
  background: #FFFFFF;
  border: 1px solid #F0F0F0;
}
.textProd1 h4 {
  padding: 30px 10px;
  text-align: left;
}
.textProd1 table {
  display: none;
}
.textProd1 .yesProd,
.textProd1 .maybeProd,
.textProd1 .notProd {
  position:relative;
  padding:5px 30px;
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 15px;
  bottom:14.5px;
  background: #ffffff;
  color: #52BD10
}
.textProd1 .maybeProd {
  color: #E7A806;
}
.textProd1 .notProd {
  color: #ED5959;
}
.textProd1 .blockPay1 {
  display:flex;
  align-items: center;
  justify-content: space-between;
}
.blockPay1 .payLink1 {
  padding:13px 13px 13px 53px;
}
.payLink1 .payIcon1 {
  width:30px;
  height:33px;
  left: 13px;
  bottom: 10px;
}
.blockPay1 .size1 {
  font:normal 600 2.4rem 'AvenirNextCyr';
  line-height:26px;
  color: #5C5C5C;
}
.blockPay1 .size1,
.blockPay1 .payLink1 {
  margin:10px;
}
.blockPay1 .size1 {
  width:130px;
}
.blockPay1 .payLink1 {
  width:150px;
}
/*--------------- //popularProduct -------------*/
/*--------------- about -------------*/
.about .backImg {
  background: linear-gradient(242.85deg, #FFFFFF 18.39%, rgba(255, 255, 255, 0) 57.47%)/*, url('../img/image_back_about.png')*/;
  /*background-size: cover;*/
  width:100%;
}
.flex3 .blockText2 {
  padding:0 10px;
}
.blockText2 h1 {
  text-align:left;
  margin:27px auto;
}
.blockText2 p {
  text-align:left;
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 24px;
  color: #000000;
}
.blockText2 .link {
  width:250px;
  position:relative;
  left:calc(50% - 125px);
  font:normal 600 1.6rem 'AvenirNextCyr';
  line-height:18px;
  text-align: center;
  letter-spacing: 0.15em;
  margin:56px 0;
  padding: 15px;
  color: #F4A876;
  border: .1rem solid #F4A876;
}
/*--------------- //about -------------*/
/*---------------- testimonials ---------------*/
.wrapper .testimonials {
  margin-top: 39px;
  margin-bottom:60px;
  border-top:.1rem solid #F4A876;
}
.flex4 h2 {
  position:relative;
  top:-44px;
  width: 270px;
  margin:0 auto;
  background: #ffffff;
}
.slider3 .user {
  position: relative;
  text-align:center;
  margin:50px 10px 30px 10px;
  padding:15px 5px;
  background: #FFFFFF;
  border: 1px solid #E5E5E5;
}
.user .imgProd2 {
  position:absolute;
  width:90px;
  height:90px;
  margin:0 auto;
  top:-45px;
  left:calc(50% - 45px);
  border-radius:50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.user h4 {
  margin:45px 0 15px 0;
  line-height: 22px;
  color: #F4A876;
}
.user p {
  margin-bottom:10px;
}
.user .userName {
  font:normal 1.8rem 'AvenirNextCyr';
  line-height: 25px;
  text-align: center;
  color: #C4C4C4;
}
.slider3 .slick-dots {
  bottom: -20px;
}

/*---------------- //testimonials ---------------*/
